import React from 'react'
import {Table, Icon} from 'antd'
import {ColumnProps} from 'antd/lib/table'
import {view} from 'react-easy-state'

import {Classifier} from '.'
import {classifierStore} from './store'

const columns: ColumnProps<Classifier>[] = [
  {
    title: '',
    dataIndex: 'name',
    key: 'name'
  },
  {
    title: ' .',
    dataIndex: 'namePlural',
    key: 'namePlural'
  },
  {
    title: '   ',
    dataIndex: 'split',
    key: 'split',
    render: val => val && ''
  },
  {
    title: '  ',
    dataIndex: 'useInTransfer',
    key: 'useInTransfer',
    render: val => val && ''
  }
]

type Props = {
  onRowClick: (classifierId: string) => any
}

export const ClassifiersTable = view((props: Props) => {
  const {onRowClick} = props
  const {classifiersArr} = classifierStore

  if (classifiersArr.length === 0) {
    return <Icon type="loading" />
  }

  return (
    <Table<Classifier>
      dataSource={classifiersArr}
      columns={columns}
      size="middle"
      rowKey="id"
      onRow={record => ({
        onClick: () => onRowClick(record.id)
      })}
      pagination={false}
    />
  )
})
